* {
  font-family: "Roboto Mono", monospace;
  font-weight: 300;
}

.root {
  padding: 1rem;
  display: flex;
  justify-content: center;
}

.wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
}

.day {
  padding: 0.75rem 1rem 1rem 1rem;
  border-radius: 4px;
  border: 1px solid #282c34;
  box-shadow: 2px 2px 1px rgba(0, 0, 0, 0.2);
}

.day + .day {
  margin-top: 0.5rem;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.title {
  font-weight: 400;
}

.summary-wrapper {
  display: flex;
}

.summary {
  display: block;
  margin-left: 1rem;
  padding: 0.2rem 0.5rem;
  border-radius: 5px;
  border: 1px dashed #282c34;
}

.entry {
  position: relative;
  display: flex;
  margin-top: 0.5rem;
}

.entry-controls {
  visibility: hidden;
  position: absolute;
  right: 0;
}

button.entry-control {
  height: 1rem;
  width: 1rem;
  margin-left: 0.5rem;
}

.entry-control.edit {
  background-color: green;
}

.entry-control.delete {
  background-color: red;
}

.entry:hover .entry-controls {
  visibility: visible;
}

.timespan {
  display: flex;
}

.separator {
  padding: 0 0.25rem;
}

input {
  font-size: 1rem;
  border: none;
  background-color: transparent;
}

input:focus {
  outline: none;
}

input[type="time"]::-webkit-datetime-edit-hour-field,
input[type="time"]::-webkit-datetime-edit-minute-field {
}

input[type="time"]::-webkit-calendar-picker-indicator {
  display: none;
}

input[type="text"] {
  padding: 0;
}

.description {
  width: 100%;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  margin-left: 1rem;
}

.entry-summary {
  font-size: 0.825rem;
}

.add-today {
  margin-top: 1rem;
}

.controls-wrapper {
  position: absolute;
  right: -20px;
}

.controls {
  display: block;
  position: absolute;
  left: 0px;
}

.input-wrapper {
  display: flex;
  margin-top: 0.5rem;
}
